                  <dv-border-box-8 class="box-padding">
                    <div class="new-mgb">
                      <span>
                        <icon
                          name="chart-pie"
                          class="text-icon new-title"
                        ></icon>
                      </span>
                      <span class="fs-xl text mx-2 mb-1 pl-3 new-title"
                        >区考核排名</span
                      >
                    </div>
                    <dv-capsule-chart
                      :config="config_right_bottom"
                      class="flex-1"
                    />
                  </dv-border-box-8>
                </div>
              </div>
            </div>
            <div class="height100">
              <div class="height100 flex column">
                <div class="flex-1">
                  <dv-border-box-8 class="box-padding">
                    <div class="new-mgb">
                      <span>
                        <icon
