                    <newLeftBottom class="flex-1"></newLeftBottom>
                  </dv-border-box-8>
                </div>
                <div class="flex-1">
                  <dv-border-box-8 class="box-padding">
                    <div class="new-mgb">
                      <span>
                        <icon
                          name="chart-pie"
                          class="text-icon new-title"
                        ></icon>
                      </span>
                      <span class="fs-xl text mx-2 mb-1 pl-3 new-title"
                        >第三方事件</span
                      >
                    </div>
                    <dv-scroll-board :config="config_right_mid" class="flex-1" />
                  </dv-border-box-8>
                </div>
                <div class="flex-1">
