<template>
  <div class="height100">
      <div id="map" class="cover"></div>
  </div>
</template>

<script>
// 
import AMap from 'AMap'
import Loca from 'Loca'
export default {
  data () {
    return {
    };
  },

  components: {},

  computed: {},

  mounted() {
      this.loadMap2()
  },

  methods: {
      /* eslint-disable */
      loadMap2() {
          var map = ( new AMap.Map('map', {
                zoom: 13,
                center: [116.352734,39.8447],
                showLabel: false,
                mapStyle: 'amap://styles/84c079d3a13747f754531889f4d67b83',
                pitch: 50,
            }));

            var loca = new Loca.Container({
                map,
            });
            window._loca = loca;

            var geo = new Loca.GeoJSONSource({
                url: 'https://a.amap.com/Loca/static/loca-v2/demos/mock_data/bj_bus.json',
            });

            var layer = new Loca.PulseLineLayer({
                // loca,
                zIndex: 10,
                opacity: 1,
                visible: true,
                zooms: [2, 22],
            });

            var headColors = ['#EFBB51', '#7F3CFF', '#4CC19B', '#0B5D74', '#E06AC4', '#223F9B', '#F15C1A', '#7A0FA6'];

            console.log('geo', geo);
            layer.setSource(geo);
            layer.setStyle({
                altitude: 0,
                lineWidth: 2,
                // 脉冲头颜色
                headColor: (_, feature) => {
                    return headColors[feature.properties.type - 1];
                },
                // 脉冲尾颜色
                trailColor: 'rgba(128, 128, 128, 0.5)',
                // 脉冲长度，0.25 表示一段脉冲占整条路的 1/4
                interval: 0.25,
                // 脉冲线的速度，几秒钟跑完整段路
                duration: 5000,
            });
            loca.add(layer);
            loca.animate.start();

            // 图例
            var lengend = new Loca.Legend({
                loca: loca,
                title: {
                    label: '公交类型',
                    fontColor: 'rgba(255,255,255,0.4)',
                    fontSize: '16px'
                },
                style: {
                    backgroundColor: 'rgba(255,255,255,0.1)',
                    left: '120px',
                    bottom: '40px',
                    fontSize: '12px'
                },
                dataMap: [
                    { label: 'A类型', color: headColors[7] },
                    { label: 'B类型', color: headColors[6] },
                    { label: 'C类型', color: headColors[5] },
                    { label: 'D类型', color: headColors[4] },
                    { label: 'E类型', color: headColors[3] },
                    { label: 'F类型', color: headColors[2] },
                    { label: 'G类型', color: headColors[1] },
                    { label: 'H类型', color: headColors[0] },
                ],
            });

            loca.animate.start();
            var dat = new Loca.Dat();
            dat.addLayer(layer, '公交');
      },
      loadMap() {
          let that = this
      this.map = new AMap.Map('map', {
          zoom:11,//级别
          center: [116.397428, 39.90923],//中心点坐标
          resizeEnable: true,
          zoom: 12,
      });
      // 插件
      AMap.plugin(['AMap.ToolBar', 'AMap.Scale'], function () {
        that.map.addControl(new AMap.ToolBar())
        that.map.addControl(new AMap.Scale())
      })
      // marker标记
      this.marker = new AMap.Marker({
         position: null
      })
      this.map.add(this.marker);
      // 绘制折线
      this.path = new AMap.Polyline({
        path: null,
        isOutline: false,     //线条是否带描边，默认false
        outlineColor: '#ffeeff',//线条描边颜色，此项仅在isOutline为true时有效，默认：#000000
        borderWeight: 1,    //描边的宽度，默认为1
        strokeColor: "#3366FF", //线条颜色，使用16进制颜色代码赋值。默认值为#006600
        strokeOpacity: 1,   //线条透明度，取值范围[0,1]，0表示完全透明，1表示不透明。默认为0.9
        strokeWeight: 2,    //线条宽度，单位：像素
        // 折线样式还支持 'dashed'
        strokeStyle: "dashed",  //线样式，实线:solid，虚线:dashed
        // strokeStyle是dashed时有效
        strokeDasharray: [10, 5],//勾勒形状轮廓的虚线和间隙的样式，此属性在strokeStyle 为dashed 时有效
        lineJoin: 'round',    //折线拐点的绘制样式，默认值为'miter'尖角，其他可选值：'round'圆角、'bevel'斜角
        lineCap: 'round',   //折线两端线帽的绘制样式，默认值为'butt'无头，其他可选值：'round'圆头、'square'方头
        zIndex: 50,       //折线覆盖物的叠加顺序。默认叠加顺序，先添加的线在底层，后添加的线在上层。通过该属性可调整叠加顺序，使级别较高的折线覆盖物在上层显示默认zIndex：50、
      })
      // 将折线添加至地图实例
      this.map.add(this.path);
      }
  }
}

</script>
<style lang='scss' scoped>
 #map {
              width: 100%;
              height: 100%;
              margin: 0;
              padding: 0;
          }
        
          .demo-title {
              position: absolute;
              top: 25px;
              left: 25px;
              z-index: 1;
          }

          h1 {
              font-size: 18px;
              margin: 0;
              color: rgb(180, 180, 190);
          }

          h3 {
              font-size: 12px;
              font-weight: normal;
              margin-top: 5px;
              color: rgb(150,150,150);
          }
</style>